<template>
  <v-card>
    <v-card-title class="pb-0">
      <mrc_ess_switch :true-value="1"
                      :false-value="0"
                      v-model="stafflist.status"
                      :true-text="$t('label_To_enable_the')"
                      :false-text="$t('label_disable')"></mrc_ess_switch>
      <v-btn rounded
             color="#a5d2e8"
             dark
             x-small
             v-if="stafflist.staff_status==3">{{$t('label_Working')}}</v-btn>
      <v-btn rounded
             color="#ffe890"
             dark
             x-small
             v-if="stafflist.staff_status==1">{{$t('label_Probation_period')}}</v-btn>
      <v-btn rounded
             color="#ffe890"
             dark
             x-small
             v-if="stafflist.staff_status==2">{{$t('label_Get_ready_to_turn_right')}}</v-btn>
      <v-btn rounded
             color="#e5c2a2"
             dark
             x-small
             v-if="stafflist.staff_status==6">{{$t('label_Retired')}}</v-btn>
      <v-btn rounded
             color="#ffddb9 "
             dark
             x-small
             v-if="stafflist.staff_status==5">{{$t('label_Prepare_to_leave')}}</v-btn>
      <v-btn rounded
             color="#ffcff2"
             dark
             x-small
             v-if="stafflist.staff_status==0">{{$t('label_Preparetojoin')}}</v-btn>
      <v-btn rounded
             color="#e3bec8"
             dark
             x-small
             v-if="stafflist.staff_status==7">{{$t('label_Retired_office')}}</v-btn>
      <v-btn rounded
             color="#dfdfdf"
             dark
             x-small
             v-if="stafflist.staff_status==4">{{$t('label_Stop')}}</v-btn>
    </v-card-title>
    <v-card-text>
      <v-row>
        <v-col cols="4">
          <!-- <v-text-field :label="$t('label_The_name')+'*'"
                          :readonly="disabled"
                          :error-messages="$errors('stafflist','preferred_name',requiredRule.concat(nocharacter))"
                          v-model="stafflist.preferred_name"
                          >
            </v-text-field> -->
          <mrc_ess_multiText :label="$t('label_The_name')+'*'"
                             :readonly="disabled"
                             :isNational="judegNational('preferred_name')"
                             :error-messages="$errors('stafflist','preferred_name',requiredRule.concat(nocharacter))"
                             v-model="stafflist.preferred_name">
          </mrc_ess_multiText>
        </v-col>
        <v-col cols="4">
          <!-- <v-text-field label="姓氏*"
                          :readonly="disabled"
                          :error-messages="$errors('stafflist','family_name',requiredRule.concat(nocharacter))"
                          v-model="stafflist.family_name"
                          >
            </v-text-field> -->
          <mrc_ess_multiText :label="$t('label_Surname')+'*'"
                             :readonly="disabled"
                             :isNational="judegNational('family_name')"
                             :error-messages="$errors('stafflist','family_name',requiredRule.concat(nocharacter))"
                             v-model="stafflist.family_name">
          </mrc_ess_multiText>
        </v-col>
        <v-col cols="4">
          <!-- <v-text-field label="小名"
                          :readonly="disabled"
                          :error-messages="$errors('stafflist','family_name',nocharacter)"
                          v-model="stafflist.child_name">
            </v-text-field> -->
          <mrc_ess_multiText :label="$t('label_Nickname')"
                             :isNational="judegNational('family_name')"
                             :readonly="disabled"
                             :error-messages="$errors('stafflist','family_name',nocharacter)"
                             v-model="stafflist.child_name">
          </mrc_ess_multiText>
        </v-col>
        <v-col cols="8">
          <!-- <v-text-field label="显示名字*"
                          :error-messages="$errors('stafflist','show_name',requiredRule.concat(nocharacter))"
                          :readonly="disabled"
                          v-model="stafflist.show_name">
            </v-text-field> -->
          <mrc_ess_multiText :label="$t('label_Show_the_name')+'*'"
                             :isNational="judegNational('show_name')"
                             :error-messages="$errors('stafflist','show_name',requiredRule.concat(nocharacter))"
                             :readonly="disabled"
                             v-model="stafflist.show_name">
          </mrc_ess_multiText>
        </v-col>
        <v-col cols="4">
          <!-- <v-text-field label="员工ID*"
                          :error-messages="$errors('stafflist','tricode',requiredRule.concat(nocharacter))"
                          :readonly="disabled"
                          v-model="stafflist.tricode">
            </v-text-field> -->
          <mrc_ess_multiText :label="$t('label_Employee_ID')+'*'"
                             :isNational="judegNational('tricode')"
                             :error-messages="$errors('stafflist','tricode',requiredRule.concat(nocharacter))"
                             :readonly="disabled"
                             v-model="stafflist.tricode">
          </mrc_ess_multiText>
        </v-col>
        <v-col cols="12">
          <!-- <v-file-input v-model="stafflist.staff_avatar"
                          placeholder="Upload your documents"
                          label="相片"
                          multiple
                          prepend-icon="mdi-paperclip">
              <template v-slot:selection="{ text }">
                <v-chip small
                        label
                        color="primary">
                  {{ text }}
                </v-chip>
              </template>
            </v-file-input> -->
          <!-- <v-text-field label="相片"
                          :readonly="disabled"
                          v-model="stafflist.staff_avatar">
            </v-text-field> -->
          <mrc_ess_multiText :label="$t('label_Photo')"
                             :isNational="judegNational('staff_avatar')"
                             :readonly="disabled"
                             v-model="stafflist.staff_avatar">
          </mrc_ess_multiText>
        </v-col>

        <v-col cols="12">
          <!-- <v-text-field label="邮箱*"
                          :error-messages="$errors('stafflist','staff_email',[...requiredRule,...emailRule])"
                          :readonly="disabled"
                          v-model="stafflist.staff_email">
            </v-text-field> -->
          <mrc_ess_multiText :label="$t('label_Mailbox')+'*'"
                             :isNational="judegNational('staff_email')"
                             :error-messages="$errors('stafflist','staff_email',[...requiredRule,...emailRule])"
                             :readonly="disabled"
                             v-model="stafflist.staff_email">
          </mrc_ess_multiText>
        </v-col>

        <v-col cols="12">
          <mrc_ess_prompt :label="$t('label_department')+'*'"
                          :errMsg="$errors('stafflist','dept_uuid',requiredRule)"
                          :rules='false'
                          :disabled='disabled'
                          v-model="stafflist.dept_uuid"
                          :obj="companyName">
          </mrc_ess_prompt>
        </v-col>

        <v-col cols="6">
          <mrc_ess_prompt :label="$t('label_Position_Title')+'*'"
                          :errMsg="$errors('stafflist','job_post_uuid',requiredRule)"
                          :rules='false'
                          :disabled='disabled'
                          v-model="stafflist.job_post_uuid"
                          :obj="job_post_uuid">
          </mrc_ess_prompt>
        </v-col>

        <v-col cols="6">
          <mrc_ess_prompt :label="$t('label_Post_grades')+'*'"
                          :errMsg="$errors('stafflist','job_post_grade_uuid',requiredRule)"
                          :rules='false'
                          :disabled='disabled'
                          :multiple="true"
                          v-model="stafflist.job_post_grade_uuid"
                          :obj="mrbase_sys_job_post_grades">
          </mrc_ess_prompt>
        </v-col>

        <v-col cols="3">
          <v-checkbox class="mx-2"
                      :true-value="1"
                      :false-value="0"
                      color="#ff8100"
                      :readonly="disabled"
                      v-model="stafflist.is_org_manager"
                      :label="$t('label_The_person_in_charge_of_the_unit')"></v-checkbox>
        </v-col>
        <v-col cols="3">
          <v-checkbox class="mx-2"
                      :true-value="1"
                      :false-value="0"
                      :readonly="disabled"
                      v-model="stafflist.is_dept_manage"
                      color="#ff8100"
                      :label="$t('label_Department_head')"></v-checkbox>
        </v-col>

        <v-col cols="12">
          <mrc_ess_prompt :label="$t('label_Boss')"
                          :rules='false'
                          :disabled='disabled'
                          v-model="stafflist.direct_leader_code"
                          :obj="superior">
          </mrc_ess_prompt>
        </v-col>

        <v-col cols="12">
          <mrc_ess_prompt :label="$t('label_role'+'*')"
                          multiple
                          :errMsg="$errors('stafflist','role_uuids',requiredRule)"
                          :rules='false'
                          :disabled='disabled'
                          v-model="stafflist.role_uuids"
                          :obj="spica">
          </mrc_ess_prompt>
        </v-col>
      </v-row>
    </v-card-text>
    <mrc_ess_submitBtn @cancel='cancel'
                       @success='success'
                       :loading='btnIsLoading'
                       v-if="!disabled"></mrc_ess_submitBtn>
  </v-card>

</template> 

<script>
import { required, maxLength, between, email, url } from 'vuelidate/lib/validators'
import { getStaffMsgDetail, getStaffMsgUpdate } from '@/service/api/staffmsg.js'
export default {
  components: {},
  props: {
    disabled: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      btnIsLoading: false,
      stafflist: {
        job_post_uuid: "",    //所属岗位
        org_level_uuid: "",    //所属公司 
        show_name: "",    //显示名字
        job_post_grade_uuid: "", //岗位等级
        given_name: "",    //名
        child_name: "",    //小名
        christian_name: "",    //员工洋名
        role_uuids: "",    //所属角色
        dept_uuid: "",    //所属部门
        family_name: "",    //员工姓
        preferred_name: "",    //员工姓名
        staff_email: "",    //邮箱
        is_dept_manage: 0,        //	是否部门负责人 1是 0否
        is_org_manager: 0,        //	是否单位负责人 1是 0否
        is_dept_manage: 0,
        direct_leader_code: '',    //	直属领导
        staff_avatar: '展示不同自己添加',	//头像
        tricode: '',//员工id
        status: 1  //默认传1
      },
      files: [],
    };
  },
  validations: {
    stafflist: {
      preferred_name: {
        required, nocharacter (value) {
          return this.$myVerify.nocharacter(value)
        }
      },
      family_name: {
        required, nocharacter (value) {
          return this.$myVerify.nocharacter(value)
        }
      },
      child_name: {
        nocharacter (value) {
          return this.$myVerify.nocharacter(value)
        }
      },
      tricode: {
        required, nocharacter (value) {
          return this.$myVerify.nocharacter(value)
        }
      },
      show_name: {
        required, nocharacter (value) {
          return this.$myVerify.nocharacter(value)
        }
      },
      staff_email: {
        required,
        email
      },
      dept_uuid: {
        required
      },
      job_post_uuid: {
        required
      },
      job_post_grade_uuid: {
        required
      },
      is_org_manager: {

      },
      is_dept_manage: {

      },
      direct_leader_code: {

      },
      role_uuids: {
        required
      },
    }
  },
  created () { },
  async mounted () {
    await this.national('mrbase_sys_staffs')
    if (this.$store.state.uuid) {
      await this.getstaffdetail()
    }
  },
  methods: {
    async getstaffdetail () { //获取数据
      const uuid = this.$store.state.uuid
      const res = await getStaffMsgDetail({ uuid })
      if (res.code == 200) {
        this.stafflist = res.data
        // console.log(this.stafflist);
        this.stafflist.role_uuids = this.stafflist.role_uuids.indexOf(',') != -1 ? this.stafflist.role_uuids.split(',') : [this.stafflist.role_uuids]
        this.stafflist.job_post_grade_uuid = res.data.job_post_grade_uuid === '' ? [] : res.data.job_post_grade_uuid.split(',')
      }
      else {
        this.$store.commit('newsTips', res.msg)
      }

    },
    cancel () {
      this.$myConfirm({
        content: '确认取消编辑'
      }).then(res => {
        this.$router.push({
          name: 'staffmanagement'
        })
      }).catch(err => { })
    },
    success () {
      // console.log(this.stafflist.status);
      this.$v.stafflist.$touch()
      if (this.$v.stafflist.$error) return
      this.btnIsLoading = true
      this.stafflist.org_level_uuid = this.company_uuid
      this.stafflist.role_uuids = this.stafflist.role_uuids.join()
      delete this.stafflist.created_by
      delete this.stafflist.created_utc_datetime
      delete this.stafflist.updated_by
      delete this.stafflist.updated_utc_datetime
      if (!this.stafflist.is_dept_manage) {
        this.stafflist.is_dept_manage = 0
      }
      if (!this.stafflist.is_org_manager) {
        this.stafflist.is_org_manager = 0
      }
      if (!this.stafflist.is_dept_manage) {
        this.stafflist.is_dept_manage = 0
      }
      // this.stafflist.status = 1
      if (Array.isArray(this.stafflist.job_post_grade_uuid)) this.stafflist.job_post_grade_uuid = this.stafflist.job_post_grade_uuid.join()
      getStaffMsgUpdate(this.stafflist).then(async res => {
        if (res.code === '200') {
          this.$store.commit('newsTips', this.$t('message_The_operation_was_successful'))
          await this.getstaffdetail()
          this.btnIsLoading = false
        } else {
          this.$store.commit('newsTips', this.$t('message_The_modification_failed'))
          this.btnIsLoading = false
        }
      })
    }
  },
  computed: {
    companyName () {  //部门参照查询字段
      return { prompt_name: 'levelUuid_deptUuid', org_level_uuid: this.company_uuid }
    },
    company_uuid () { //当前选中公司
      return this.$store.state.currentCompany
    },
    job_post_uuid () {  //岗位查询
      // return { prompt_name: 'level-dept-post', dept_uuid: 'd83de2d3-36bb-47f7-9f43-3f18dd295bbb' }
      return { prompt_name: 'level-dept-post', dept_uuid: this.stafflist.dept_uuid }
      // return { prompt_name: 'level-dept-post', org_level_uuid: this.company_uuid, dept_uuid: this.stafflist.dept_uuid }
    },
    superior () { //上司高级参照
      return { prompt_name: 'boss', org_level_uuid: this.company_uuid, staff_uuid: this.$store.state.uuid }
    },
    spica () { //角色高级参照 
      // return { prompt_name: 'level-role', org_level_uuid: 'd83de2d3-36bb-47f7-9f43-3f18dd295bbb' }
      return { prompt_name: 'level-role', org_level_uuid: this.company_uuid }
      // return { prompt_name: 'level-role', company_uuid: this.company_uuid }
    },
    mrbase_sys_job_post_grades () {
      return { prompt_name: 'mrbase_sys_job_post_grades', org_level_uuid: this.company_uuid }
    }
  },
  watch: {
  },
};
</script>

<style scoped>
</style>
  